* {
  box-sizing: border-box;
}

.page {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.page__body {
  margin: 0;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  max-width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image {
  object-fit: cover;
  width: 160px;
  height: 134px;
  margin: 0 auto;
}

.card__description {
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__reviews {
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__label {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__amount {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__grade {
  justify-content: space-between;
  align-items: end;
  width: 100%;
  margin-top: 16px;
  display: flex;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  justify-content: center;
  margin-top: 16px;
  padding: 11px 70px 13px;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__link {
  color: inherit;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
